import { hash } from "../../../utils/hash";
export function masonryUtils({ element, child }) {
    const { settings } = element;
    const itemsPerRowDesktop = settings.itemsPerRow.desktop?.value;
    const itemsPerRowTablet = settings.itemsPerRow.tablet?.value ?? settings.itemsPerRow.desktop?.value;
    const itemsPerRowMobile = settings.itemsPerRow.mobile?.value ?? settings.itemsPerRow.desktop?.value;
    const itemSpacingDesktop = settings.itemSpacing.desktop?.value;
    const itemSpacingTablet = settings.itemSpacing.tablet?.value ?? settings.itemSpacing.desktop?.value;
    const itemSpacingMobile = settings.itemSpacing.mobile?.value ?? settings.itemSpacing.desktop?.value;
    const observed = hash(JSON.stringify(settings) +
        JSON.stringify(child.map((chld) => {
            return {
                ...chld,
                collapsed: false,
            };
        })));
    return {
        itemsPerRowDesktop,
        itemsPerRowTablet,
        itemsPerRowMobile,
        itemSpacingDesktop,
        itemSpacingTablet,
        itemSpacingMobile,
        observed
    };
}
